

function Questionnaires(props){
    const history = useHistory();

    const [questionnaires, setQuestionnaires] = useState([]);

    const getAllQuestionnaires = () =>{
        fetch(globals.url+ "getAllQuestionnaires")
            .then(res => res.json())
            .then(res => setQuestionnaires(res))
    };

    useEffect(()=> {
        getAllQuestionnaires();
    },[])

    return(
        <Container>
            <Col md={{span: 9, offset: 2}} sm={{span: 8, offset: 2}} lg={{span: 7, offset: 3}} xl={{span: 4, offset: 4}}>
            <Form.Group controlId="exampleForm.ControlSelect1">
                <Form.Label>Questionnaires</Form.Label>
                <Form.Control as="select">
                    {questionnaires.map(questionnaire => <option>{questionnaire.nomQuestionnaire}</option>)}
                </Form.Control>
            </Form.Group>
            </Col>
        </Container>
    );
}

export default Questionnaires;