import React, { useEffect, useState }  from "react";
import ReactDOM from "react-dom";
import { useHistory, BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { Col, Container, Row } from "react-bootstrap"; 

import 'bootstrap/dist/css/bootstrap.min.css';
import './css/main.css';

import globals from "./config/globals";
import logo from "./images/chatsport.png";

import LoginForm from "./form/LoginForm";
import Questionnaires from "./Questionnaires";

function Index(props){
    const history = useHistory();
    
    const [sportifs, setSportifs] = useState([]);
    const [error, setError] = useState("");

    const getAllSportifs = () =>{
        fetch(globals.url+ "getAllSportifs")
            .then(res => res.json())
            .then(res => setSportifs(res))
    };

    function verifySportif (dataSportif) {
        const requestOptions = {
            method: 'POST',
            headers: {"Content-type": "application/json; charset=UTF-8"},
            body: JSON.stringify(dataSportif)
        };
        
        fetch(globals.url + "verifySportif", requestOptions)
            .then(res => res.json())
            .then(res => {
                if(res.ok){
                    history.push("/questionnaires")
                } else{
                    setError(res.error)
                }
            })
    };

    useEffect(()=> {
        getAllSportifs();
    },[])

    return(
        <Container>
            <Row>
                <Col md={{span: 9, offset: 2}} sm={{span: 8, offset: 2}} lg={{span: 7, offset: 3}} xl={{span: 4, offset: 4}}>
                    <img src={logo} alt="Logo" width="180" height="148"/>
                    {error !== ""  
                        ?  <h2 className="text-danger">{error}</h2> 
                        :  <h2>Se connecter</h2>
                    }
                    <LoginForm onLogin={verifySportif}/>            
                    {sportifs.map(sportif => sportif.pseudoSportif)}           
                </Col>        
            </Row>
            <Router>
                <Switch>
                    <Route path="/questionnaires">
                        <Questionnaires />
                    </Route>
                </Switch>
            </Router>
        </Container>
    );
}

ReactDOM.render(
    <Index />,
    document.getElementById('root')
);